import * as React from 'react'
import Head from 'next/head'
import {ISeo} from '../interfaces'

type IProps={
  seo:ISeo
}
const HeadTag: React.FunctionComponent<IProps> = ({seo}) => (

<Head>
    <title>{seo.title}</title>
    <meta charSet='utf-8' />
    <meta name='viewport' content='initial-scale=1.0, width=device-width' />
    <meta name="keywords" content={seo.keywords.toString()} />
    <title>{seo.title}</title>
    <meta name="description" content={seo.description} />

    <meta itemProp="name" content={seo.title}/>
    <meta itemProp="description" content={seo.description}/>
    <meta itemProp="ima" content={seo.image}/>

    <meta name="twitter:card" content="summary_large_image"/>
    <meta name="twitter:title" content={seo.title}/>
    <meta name="twitterge:description" content={seo.twitterDescription}/>
    <meta name="twitter:creator" content="@sanyamarya"/>
    <meta name="twitter:image:src" content={seo.image}/>

    <meta property="og:title" content={seo.title} />
    <meta property="og:type" content={seo.type} />
    <meta property="og:url" content={seo.url} />
    <meta property="og:image" content={seo.image} />
    <meta property="og:description" content={seo.description} />
    <meta property="og:site_name" content="sanyamarya" />
    
   {seo.article && <meta property="article:published_time" content={seo.article.published_on} />}
    {seo.article && <meta property="article:section" content={seo.article.section}/>}
    {seo.article && <meta property="article:tag" content={seo.keywords.join(' ')} />}

    <script src="https://cdn.lightwidget.com/widgets/lightwidget.js"></script>
    {/* <script src="https://snapwidget.com/js/snapwidget.js"></script> */}

    <link rel="icon" href="demo_icon.gif" type="image/gif" sizes="16x16" />
    </Head>

)

export default HeadTag
